<!-- filepath: c:\programy\xampp\htdocs\notatnik\frontend\src\components\HomePage.vue -->
<template>
  <article>
    <h2>Witaj w notatniku</h2>
    <p v-if="loggedIn">
      👋 Witaj {{ userName }}, jesteś zalogowany! Możesz korzystać z dodatkowych
      funkcji.
    </p>
    <p v-else>
      🔑 Nie jesteś zalogowany.
      <router-link to="/login">Zaloguj się</router-link>, aby uzyskać więcej
      możliwości.
    </p>
    <p>
      Notatnik to aplikacja, która pozwala na zapisywanie notatek. Możesz
      zapisywać notatki, edytować je, usuwać i przeglądać. Wszystko to zrobisz w
      łatwy i przyjemny sposób.
    </p>
    <p>
      Aby korzystać z notatnika, musisz się zalogować. Jeśli nie masz konta,
      możesz je założyć. Wystarczy, że klikniesz w odpowiednią opcję w menu
      nawigacyjnym.
    </p>
  </article>
</template>

<script>
export default {
  name: "HomePage",
  data() {
    return {
      loggedIn: false,
      userName: "",
    };
  },
  async mounted() {
    try {
      const response = await fetch(
        "http://localhost/notatnik/backend/api/check_session.php",
        {
          credentials: "include",
        }
      );
      const data = await response.json();
      this.loggedIn = data.logged_in;
      this.userName = data.user_name || "";
    } catch (error) {
      console.error("Błąd podczas sprawdzania sesji:", error);
    }
  },
};
</script>
