<template>
  <article>
    <h2>Rejestracja</h2>
    <form @submit.prevent="handleRegister">
      <label for="login">Login</label>
      <input
        type="text"
        name="login"
        class="username"
        required
        placeholder="np. Mariusz12"
        v-model="userName"
      />
      <p v-if="loginError" class="error">
        Użytkownik o takim loginie już istnieje.
      </p>

      <label for="password">Hasło</label>
      <input
        type="password"
        name="password"
        class="password"
        required
        placeholder="Wprowadź hasło"
        v-model="password"
      />

      <label for="repeatedPassword">Powtórz hasło</label>
      <input
        type="password"
        name="repeatedPassword"
        class="password"
        required
        placeholder="Powtórz hasło"
        v-model="repeatedPassword"
      />
      <p v-if="passwordMismatchError" class="error">
        Hasła się nie zgadzają! Proszę spróbować ponownie.
      </p>

      <p v-if="generalError" class="error">Proszę wypełnić wszystkie pola.</p>
      <p v-if="registrationSuccess" class="success">
        Rejestracja zakończona sukcesem!
      </p>

      <button type="submit" class="submit-btn">Zarejestruj</button>
    </form>
  </article>
</template>

<script>
export default {
  name: "RegisterPage",
  data() {
    return {
      registerData: {
        login: "",
        password: "",
        repeatedPassword: "",
      },
      errorMessage: "",
      successMessage: "",
    };
  },
  methods: {
    async register() {
      try {
        const response = await fetch(
          "http://localhost/notatnik/backend/api/register.php",
          {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify(this.registerData),
          }
        );
        const data = await response.json();
        if (response.ok) {
          this.successMessage = data.success;
          this.errorMessage = "";
        } else {
          this.errorMessage = data.error;
          this.successMessage = "";
        }
      } catch (error) {
        this.errorMessage = "Wystąpił błąd podczas rejestracji.";
      }
    },
  },
};
</script>
